:root {
    /* color */
    --color-white-1: #ffffff;
    --color-white-2: #dddddd;
    --color-black-1: #000000;
    --color-black-2: #111111;
    --color-black-3: #333333;
    --color-feature: rgb(33, 107, 168);
    /* font-weight */
    --font-weight-light: 400;
    --font-weight-semi-bold: 500;
    --font-weight-bold: 700;
    /* cursor */
    --cursor-pointer: pointer;
    /* transition */
    --animation-duration: 300ms;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    cursor: default;
}

body {
    position: relative;
}

li {
    list-style: none;
}

button,
input {
    outline: none;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    padding: 1.25rem 2.5rem;
    color: var(--color-black-2);
    background-color: var(--color-white-2);
}

.hidden {
    display: none;
}

.containers {
    width: 100%;
    height: 100vh;
    background-color: var(--color-black-2);
}

#navbar {
    display: flex;
    align-items: center;
    padding: 18px 48px;
    color: var(--color-white-2);
    border-bottom: 2px solid var(--color-feature);
}

.nav-title {
    font-size: 2rem;
    font-weight: var(--font-weight-bold);
    margin-right: 5rem;
    cursor: var(--cursor-pointer);
    color: var(--color-feature);
}

.nav-menu {
    display: flex;
}

.nav-items {
    padding: 8px 12px;
    cursor: var(--cursor-pointer);
    border-bottom: 1px solid transparent;
    transition: var(--animation-duration) all;
}

.nav-items:hover {
    border-bottom: 1px solid var(--color-feature);
    color: var(--color-feature);
}
